import ReactPlayer from "react-player";
import heart from "../heartw.mp4";
import "./styles.css"
import { useTheme ,ThemeProvider } from "./ThemeContext";
import ControledFmValidation from "./SignUp/ControledFmValidation";
import Boot from "./SignUp/ControledFmValidation";
import styled from "styled-components";
import { useUser ,UserProvider } from './SignUp/UserContext';
import Submited from "./SignUp/Submited";
import { useState } from "react";

const Container = styled.div`
display: flex;
max-width: 100vw;
`;


function Home() {
  const {user} = useUser();
  const {theme} = useTheme();
    return (
    <Container>
        <div>
          <Boot />
        </div>
        <div>
          <h2 className={`head ${theme == 'dark' ? 'head-b' : ''}`}>welcome {user.name}! watch the new song</h2>;
          <div style={{paddingLeft:'30%'}}><ReactPlayer volume={0.3} url={heart} height={300} width={600} controls/></div>
        </div>
    </Container>
      );
}
function Noot() {
  return (
    <UserProvider>
      <Home />
    </UserProvider>
  );
}
export default Noot;