import { useState } from "react"; 
import "./Calc.css";
import "./styles.css"
import { ThemeProvider ,useTheme } from "./ThemeContext";

function Calc() {  
  const {theme} = useTheme();  
  let [result, setResult] = useState(''); 
  const [show, setShow] = useState(0); 

  function handleChange (e) {
    setResult(e.target.value);
  }
 
  function plus(e) { 

    e.preventDefault();
     setShow(parseInt(result) + parseInt(show));
  }; 
 
  function minus(e) { 
    let a
  	// Add the code for the minus function 
    e.preventDefault();
     setShow(parseInt(show) - parseInt(result));
  };
 
  function times(e) { 
    // Add the code for the plus function 
    e.preventDefault();
    setShow(parseInt(show) * parseInt(result));
  }; 
 
  function divide(e) { 
    // Add the code for the divide function 
    e.preventDefault();
    parseInt(result) ? setShow(parseInt(show) / parseInt(result)) : alert('Error don\'t divide by zero');
  };
 
  function resetInput(e) { 
    // Add the code for the resetInput function 
    e.preventDefault();
    setResult('');

  }; 
 
  function resetResult(e) { 
  	// Add the code for the resetResult function 
    e.preventDefault();
    setShow(0);
  }; 
 
  return ( 
    <div className="App"> 
      <div> 
        <h1 className={`${theme == 'dark' ? 'head-b' : ''}`}>Simplest Working Calculator</h1> 
      </div> 
      <form> 
        <p className={`${theme == 'dark' ? 'head-b' : ''}`}> 
          {show} 
        </p>
        <input
          pattern="[0-9]" 
          value={result}
          type="number" 
          placeholder="Type a number"
          onChange={handleChange}
        /> 
        <button onClick={plus}>add</button> 
        {/* Add the subtract button */} 
        <button onClick={minus}>subtract</button>
        {/* Add the multiply button */} 
        <button onClick={times}>multiply</button>
        {/* Add the multiply button */} 
        <button onClick={divide}>divide</button>
        {/* Add the resetInput button */} 
        <button onClick={resetInput}>resetInput</button>
        {/* Add the resetResult button */} 
        <button onClick={resetResult}>resetResult</button>
      </form> 
    </div> 
  ); 
} 
 
export default Calc; 
