import {useState} from "react"; 
import {validateEmail} from "./validateEmail"; 
import { useUser ,UserProvider } from './UserContext';
import styled  from "styled-components";
import { useTheme ,ThemeProvider } from "../ThemeContext";

//styled component; 
const AppContainer = styled.div`
  text-align: center;
  width: 30vw;
`;

const Input = styled.input`
  width: 100%;
  border-radius: 10px;
  margin: 5px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  border-color: rgb(199, 185, 185);
  padding-left: 5px;
`;

const Fieldset = styled.fieldset`
  margin: 0;
  padding-right: 30px;
  border-color: rgb(255, 247, 247);
  width: 100%;
`;

const FieldError = styled.p`
  color: red;
`;

const Sup = styled.sup`
  color: red;
`;

const Label = styled.label`
  font-weight: bold;
`;

const Putton = styled.button` 
  border: none;
  padding: 12px 24px;
  font-size: 16px;
  border-radius: 4px;
  box-shadow: 0px 2px 4px rgba(0, 0, 0, 0.1);
  margin: 10px;
`;

const Heading = styled.h2`
  color: blueviolet;
`;

const Heading2 = styled.h3`
  color: blueviolet;
`;

const Heading3 = styled.h3`
  color: skyblue;
`;

 
const PasswordErrorMessage = () => { 
 return ( 
   <FieldError>Password should have at least 8 characters</FieldError> 
 ); 
}; 

const EmailErrorMessage = () => { 
    return ( 
      <FieldError>Enter valid email</FieldError> 
    ); 
   };
   const RoleErrorMessage = () => { 
    return ( 
      <FieldError className="FieldError">set Artist</FieldError> 
    ); 
   }; 
 
function ControledFmValidation() { 
 const {theme} = useTheme();
 const {user ,updateUser} = useUser();
 const [firstName, setFirstName] = useState(""); 
 const [lastName, setLastName] = useState(""); 
 const [email, setEmail] = useState({em: ""}); 
 const [submitted, setSubmitted] = useState(false);
 const [password, setPassword] = useState({ 
   value: "", 
   isTouched: false, 
 }); 
 const [role, setRole] = useState("Artist"); 

 const getIsFormValid = () => { 
   return ( 
     firstName && 
     validateEmail(email.em) && 
     password.value.length >= 8 && 
     role !== "Artist" 
   ); 
 }; 
 
 const clearForm = () => { 
   setFirstName(""); 
   setLastName(""); 
   setEmail({em:""}); 
   setPassword({ 
     value: "", 
     isTouched: false, 
   }); 
   setRole("Artist"); 
 }; 
 
 const handleSubmit = (e) => { 
   e.preventDefault(); 
   //alert("Subscribed!"); 
   setSubmitted(true);
   updateUser({...user ,name: firstName});
   //clearForm(); 
 }; 

//conditional rendering
if (!submitted) {
  return ( 
    <AppContainer> 
      <form onSubmit={handleSubmit}> 
        <Fieldset> 
          <Heading>{user.name} Subscribe to New Songs</Heading> 
          <div className="Field"> 
            <Label htmlFor='fn' className={`${theme == 'dark' ? 'head-b' : ''}`}> 
              Name <Sup>*</Sup> 
            </Label> 
            <Input 
              id='fn'
              value={firstName} 
              onChange={(e) => { 
                setFirstName(e.target.value); 
              }} 
              placeholder="First name" 
            /> 
          </div> 
          <div className="Field"> 
            <Label className={`${theme == 'dark' ? 'head-b' : ''}`}> 
              Email address <Sup>*</Sup> 
            </Label> 
            <Input 
              value={email.em} 
              onChange={(e) => { 
                setEmail({...email, em: e.target.value ,isValid: validateEmail(e.target.value)}); 
              }} 
              placeholder="Email address" 
              onFocus={() => { 
                 setEmail({ ...email, isTouched: true });
              }}
            /> 
            {email.isTouched && !email.isValid ? ( 
              <EmailErrorMessage /> 
            ) : null}
          </div> 
          <div className="Field"> 
            <Label className={`${theme == 'dark' ? 'head-b' : ''}`}> 
              Password <Sup>*</Sup> 
            </Label> 
            <Input 
              value={password.value} 
              type="password" 
              onChange={(e) => { 
                setPassword({ ...password, value: e.target.value }); 
              }} 
              onFocus={() => { 
                setPassword({ ...password, isTouched: true }); 
              }} 
              placeholder="Password" 
            /> 
            {password.isTouched && password.value.length < 8 ? ( 
              <PasswordErrorMessage /> 
            ) : null} 
          </div> 
          <div className="Field"> 
            <Label className={`${theme == 'dark' ? 'head-b' : ''}`}> 
              Artist <Sup>*</Sup> 
            </Label> 
            <select value={role} onChange={(e) => setRole(e.target.value)}> 
              <option value="Artist">Artist</option> 
              <option value="Justin bieber">Justin bieber</option> 
              <option value="cardi B">cardi B</option> 
              <option value="Selena Gomez">Selena Gomez</option>
            </select> 
          </div> 
          {role === 'Artist' ? <RoleErrorMessage /> : null}
          <Putton type="submit" disabled={!getIsFormValid()}> 
            Subscribe 
          </Putton> 
        </Fieldset> 
      </form> 
    </AppContainer> 
  ); 
}
else {
  return ( 
    <AppContainer> 
        <Fieldset> 
          <Heading>Thank You {user.name} for subscribing</Heading> 
          <Heading2>You will be notified of any new song of</Heading2>
          <Heading3>{role}</Heading3>
          <Heading2>Via Email</Heading2>
        </Fieldset> 
    </AppContainer> 
  );
}
}  
function Boot() {
  return (
    <UserProvider>
      <ControledFmValidation />
    </UserProvider>
  );
}

export default Boot; 