import logo from './logo.svg';
import Home from './components/Home';
import Message from './components/Message';
import { Route ,Routes ,Link } from 'react-router-dom';
import './App.css';
import Calc from './components/Calc';
import { useState } from 'react';
import "./components/styles.css";
import Switch from "./components/Switch/index"
import { ThemeProvider ,useTheme } from './components/ThemeContext';
import Noot from './components/Home';

function App() {
  //state to let component link light it's color when it's focused on
  const [isFocused, setIsFocused] = useState({hom: false ,sound: false ,calc: false});
  const handleFocus1 = () => setIsFocused({hom: true ,sound: false ,calc: false});
  const handleFocus2 = () => setIsFocused({hom: false ,sound: true ,calc: false});
  const handleFocus3 = () => setIsFocused({hom: false ,sound: false ,calc: true});

  //context API
  const {theme} = useTheme();
  console.log(theme);
  
  return (
     <div className="App">
          <nav className={`nav ${theme == 'dark' ? 'nav-b' : ''}`}>
            <Link to='/' className={`nav-item  ${theme == 'dark' ? 'nav-item-b' : ''} ${isFocused.hom ? 'focused' : ''}`} onFocus={handleFocus1}>Home</Link>
            <Link to='/mess' className={`nav-item ${theme == 'dark' ? 'nav-item-b' : ''} ${isFocused.sound ? 'focused' : ''}`} onFocus={handleFocus2} style={{paddingLeft: '10px'}}>Sounds</Link>
            <Link to='/calcurator' className={`nav-item ${theme == 'dark' ? 'nav-item-b' : ''} ${isFocused.calc ? 'focused' : ''}`} onFocus={handleFocus3}>Calcurate</Link>
            <Switch />
          </nav>
          <div className={`route ${theme == 'dark' ? 'route-b' : ''}`}>
              <Routes>
              <Route path='/' element={<Home />} />
              <Route path='/mess' element={<Message />} />
              <Route path='/calcurator' element={<Calc />} />
              </Routes>
          </div>
          <div className={`footer ${theme == 'dark' ? 'footer-b' : ''}`}>
              <h2 className={`${theme == 'dark' ? 'head-b' : ''}`}>I Work hard to make People Lives easier</h2>
              <p className={`${theme == 'dark' ? 'head-b' : ''}`}>Special thanks to Pick one and binange</p>
              <p className={`${theme == 'dark' ? 'head-b' : ''}`}>Contact Patrick on <a href='https://www.facebook.com/rumamo.patrick' target='_blank'>Facebook!</a></p>
          </div>
    </div>
  );
}
function Root() {
  return (
    <ThemeProvider>
      <App />
    </ThemeProvider>
  );
}

export default Root;
